@import "../../styles/_variables.module.scss";
.todo_page_wrapper {
  display: flex;
  justify-content: center;
  .todo_page {
    @include flex_col;
    align-items: center;

    width: 70%;
    min-height: 500px;
    height: fit-content;
    margin-top: 12vh;
    padding: 20px 30px 50px 30px;

    background-color: white;
    border-radius: 2px;
    box-shadow: 0px 0px 15px 1px rgba(102, 102, 102, 0.295);
  }
}
